import {Injectable} from '@angular/core';
import { SellerUrlBase } from '../seller-url-base';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlStatistic extends SellerUrlBase {
	readonly adminUrlStatistic: string = this.sellerUrl + '/statistic';
	getStatistic(): string {
		return this.adminUrlStatistic;
	}
	getStatisticsActivatingAndInActivatingProducts(): string {
		return this.adminUrlStatistic+'/Statistics-Activating-And-InActivating-Products';
	}
	getStatisticsMonthsCancelOrder(): string {
		return this.adminUrlStatistic+'/Statistics-Months-Cancel-Order';
	}
	getStatisticsMonthsOrder(): string {
		return this.adminUrlStatistic+'/statistics-Months-Order';
	}
	getStatisticsOrderByOrderStatus(): string {
		return this.adminUrlStatistic+'/statistics-Order-By-OrderStatus';
	}
}
