import { ApiResult } from './../../../models/common/ApiResult';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { ApiResultWithList } from 'src/app/core/models/common/ApiResultWithList';
import { Result, StatisticResponse, StatisticsVerticalBarChart } from 'src/app/core/models/common/statistic/response/statistic-response.viewmodel';
import { SellerUrlStatistic } from 'src/app/core/utils/URL/seller-url/seller-statistic/seller-url-statistic';


@Injectable({
  providedIn: 'root'
})
export class SellerStatisticService {
  constructor(private sellerUrlStatistic: SellerUrlStatistic, private http: HttpClient) {}
	getStatistics(): Observable<ApiResultWithList<StatisticResponse>> {
		return this.http.get<ApiResultWithList<StatisticResponse>>(this.sellerUrlStatistic.getStatistic());
	}
	getStatisticsActivatingAndInActivatingProducts(): Observable<ApiResult<StatisticsVerticalBarChart<Result>>> {
		return this.http.get<ApiResult<StatisticsVerticalBarChart<Result>>>(this.sellerUrlStatistic.getStatisticsActivatingAndInActivatingProducts());
	}
	getStatisticsMonthsCancelOrder(): Observable<ApiResult<StatisticsVerticalBarChart<Result>>> {
		return this.http.get<ApiResult<StatisticsVerticalBarChart<Result>>>(this.sellerUrlStatistic.getStatisticsMonthsCancelOrder());
	}
	getStatisticsMonthsOrder(): Observable<ApiResult<StatisticsVerticalBarChart<Result>>> {
		return this.http.get<ApiResult<StatisticsVerticalBarChart<Result>>>(this.sellerUrlStatistic.getStatisticsMonthsOrder());
	}
	getStatisticsOrderByOrderStatus(): Observable<ApiResult<StatisticsVerticalBarChart<Result>>> {
		return this.http.get<ApiResult<StatisticsVerticalBarChart<Result>>>(this.sellerUrlStatistic.getStatisticsOrderByOrderStatus());
	}
}
