import {Component, OnInit} from '@angular/core';
import {
	Result,
	StatisticsVerticalBarChart
} from 'src/app/core/models/common/statistic/response/statistic-response.viewmodel';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {SellerStatisticService} from 'src/app/core/services/seller/seller-statistic/seller-statistic.service';
@Component({
	selector: 'app-seller-chart',
	templateUrl: './seller-chart.component.html',
	styleUrls: ['./seller-chart.component.less'],
	providers: [DestroyService]
})
export class SellerChartComponent implements OnInit {
	statisticsActivatingAndInActivatingProducts: StatisticsVerticalBarChart<Result>;
	statisticsMonthsCancelOrder: StatisticsVerticalBarChart<Result>;
	statisticsMonthsOrder: StatisticsVerticalBarChart<Result>;
	isStatisticsMonthsCancelOrderLoading = true;
	isStatisticsMonthsOrderLoading = true;
	isStatisticsActivatingAndInActivatingProductsLoading = true;
	constructor(private sellerStatisticService: SellerStatisticService, private destroy$: DestroyService) {}

	ngOnInit(): void {
		this.getStatisticsActivatingAndInActivatingProducts();
		this.getStatisticsMonthsCancelOrder();
		this.getStatisticsMonthsOrder();
	}

	getStatisticsActivatingAndInActivatingProducts() {
		this.sellerStatisticService.getStatisticsActivatingAndInActivatingProducts().subscribe((result) => {
			if (result) {
				console.log(result);
				if (result.isSuccessed) {
					if (result.resultObj) {
						this.statisticsActivatingAndInActivatingProducts = result.resultObj;
						//console.log(this.statisticsActivatingAndInActivatingProducts);
						this.isStatisticsActivatingAndInActivatingProductsLoading = false;
					}
				} else {
				}
			} else {
			}
		});
	}
	getStatisticsMonthsCancelOrder() {
		this.sellerStatisticService.getStatisticsMonthsCancelOrder().subscribe((result) => {
			if (result) {
				console.log(result);
				if (result.isSuccessed) {
					if (result.resultObj) {
						this.statisticsMonthsCancelOrder = result.resultObj;
						this.isStatisticsMonthsCancelOrderLoading = false;
					}
				} else {
				}
			} else {
			}
		});
	}
	getStatisticsMonthsOrder() {
		this.sellerStatisticService.getStatisticsMonthsOrder().subscribe((result) => {
			if (result) {
				console.log(result);
				if (result.isSuccessed) {
					if (result.resultObj) {
						this.statisticsMonthsOrder = result.resultObj;
						this.isStatisticsMonthsOrderLoading = false;
					}
				} else {
				}
			} else {
			}
		});
	}
}
