import {Injectable} from '@angular/core';
import {PagingRequestBase} from 'src/app/core/models/common/page/pagingrequest/PagingRequestBase';
import {RequestBase} from 'src/app/core/models/common/request/RequestBase';
import {SellerUrlBase} from './../seller-url-base';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlProduct extends SellerUrlBase {
	constructor() {
		super();
	}
	sellerGetProductsAPI(pagingRequestBase: PagingRequestBase, type: number): string {
		return (
			this.sellerUrl +
			'/product/products?pageindex=' +
			pagingRequestBase.pageIndex +
			'&pagesize=' +
			pagingRequestBase.pageSize +
			'&type=' +
			type
		);
	}
	sellerPostCreateProduct(): string {
		return this.sellerUrl + '/product/product';
	}
	sellerPostCreateProductTest(): string {
		return this.sellerUrl + '/product/producttest';
	}
	sellerGetProductDetailByIdAPI(requestBase: RequestBase): string {
		return this.sellerUrl + `/product/${requestBase.id}/${requestBase.languageCode}`;
	}
	sellerPostProductImagePreviewAPI(): string {
		return this.sellerUrl + `/product/image-preview`;
	}
	sellerUpdateProductAPI(): string {
		return this.sellerUrl + `/product/update`;
	}
	sellerLockProduct(): string {
		return this.sellerUrl + '/product/lock/';
	}
	sellerDeleteProduct(producId: number): string {
		return this.sellerUrl + '/product/delete/' + producId;
	}
	sellerCreateGroupProduct(): string {
		return this.sellerUrl + '/product/group-product';
	}
	sellerGetGroupProductDetail(groupProductId: number) {
		return this.sellerUrl + '/product/group-product/' + groupProductId;
	}
	sellerEditGroupProductDetail() {
		return this.sellerUrl + '/product/group-product';
	}
	sellerGetGroupProducts() {
		return this.sellerUrl + '/product/group-products';
	}
	sellerGetSearchGroupProducts(requestBase: RequestBase) {
		return this.sellerUrl + `/product/search/group-products?searchKeyWord=${requestBase.searchKeyWord}`;
	}
	sellerDeleteGroupProducts(groupProductId: number) {
		return this.sellerUrl + '/product/group-products/' + groupProductId;
	}
	sellectGetSearchProductsSelect(requestBase: RequestBase){
		return this.sellerUrl + `/product/search/product-select?searchKeyWord=${requestBase.searchKeyWord}`
	}
	sellerGetPreOrderProductDropBoxBySearchKeyWordListAPI(requestBase: RequestBase): string {
		return (
			this.sellerUrl +
			`/product/search/Pre-Order-Product?languageCode=${requestBase.languageCode}&searchKeyWord=${requestBase.searchKeyWord}`
		);
	}
}
