import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {Title} from '@angular/platform-browser';
import {Router} from '@angular/router';
import {select, Store} from '@ngrx/store';
import {filter, takeUntil} from 'rxjs/operators';
import {SellerLogin} from 'src/app/core/models/seller/login/SellerLogin';
import {SellerAppState} from 'src/app/core/store/seller/seller-app.state';
import {login} from 'src/app/core/store/seller/login/login.actions';
import {AppErrorHandler} from 'src/app/core/error-handler/app-error-handler.service';
import {NotificationService} from 'src/app/core/notification/notifications/customer/notification.service';
import {loginStateSelector} from 'src/app/core/store/seller/login/login.sellector';
import {DestroyService} from 'src/app/core/services/comon-services/destroy-service/destroy.service';
import {TranslateService} from '@ngx-translate/core';
import {SellerAccountService} from 'src/app/core/services/auth/seller/seller-account.service';
import {LocalStorageService} from 'src/app/core/services/comon-services/local-storage-service/localstorage.service';
import {SpinnerService} from 'src/app/core/services/comon-services/spinner/spinner.service';
@Component({
	selector: 'app-seller-login',
	templateUrl: './seller-login.component.html',
	styleUrls: ['./seller-login.component.scss'],
	providers: [DestroyService]
})
export class SellerLoginComponent implements OnInit {
	isSubmitted: boolean;
	returnUrl: string;
	sellerLoginForm!: FormGroup;
	count = 0;
	//observable LoginState
	constructor(
		private router: Router,
		private appTranslateService: TranslateService,
		private notificationService: NotificationService,
		private appErrorHandler: AppErrorHandler,
		private destroy$: DestroyService,
		private formBuilder: FormBuilder,
		private titleService: Title,
		private store: Store<SellerAppState>,
		private sellerAccountService: SellerAccountService,
		private localStorageService: LocalStorageService,
		private sellerSpinnerService: SpinnerService
	) {
		this.returnUrl = '';
	}

	ngOnInit(): void {
		try {
			this.controlSpinner();
			this.initForm();
			this.sub();
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}

	initForm() {
		try {
			this.sellerLoginForm = this.formBuilder.group({
				email: ['', [Validators.required, Validators.minLength(3), Validators.email]],
				password: ['', [Validators.required, Validators.minLength(8), Validators.maxLength(20)]],
				remember: [true]
			});
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}
	sub() {
		this.store
			.pipe(
				takeUntil(this.destroy$),
				select(loginStateSelector),
				filter((loginState) => loginState.message !== '')
			)
			.subscribe(
				(result) => {
					if (result.isAuthenticated) {
						if (result.message) {
							this.router.navigate(['/seller']);
							console.log(result.message);
							this.notificationService.showSuccessNotification(
								this.appTranslateService.instant(result.message)
							);
						}
					} else {
						if (result.message) {
							console.log(result.message);
							this.notificationService.showErrorNotification(
								this.appTranslateService.instant(result.message)
							);
						}
					}
					//this.isSubmitted = false;
				},
				(error) => {
					//this.isSubmitted = false;
					this.appErrorHandler.handleErrorWithNoti(error);
				},
				() => {}
			);
	}
	submitForm(): void {
		try {
			if (this.sellerLoginForm.invalid) {
				return;
			} else {
				//console.log('aaaaaaaaaaaaaaallllllllllllll');
				this.sellerSpinnerService.subjectSubmitLoadingHaveData$.next({isSubmitted: true});
				//this.isSubmitted = true;
				const val = this.sellerLoginForm.value;
				const userLogin: SellerLogin = {
					email: val.email,
					passWord: val.password,
					remember: val.remember,
					urlReturn: ''
				};
				// this.sellerAccountService
				// .login(userLogin.email, userLogin.passWord, userLogin.remember, userLogin.urlReturn)
				this.store.dispatch(login({userLogin}));
			}
		} catch (error) {
			this.appErrorHandler.handleError(error);
		}
	}

	controlSpinner() {
		this.sellerSpinnerService.subjectSubmitLoadingHaveData$.pipe(takeUntil(this.destroy$)).subscribe(
			(result) => {
				this.isSubmitted = result.isSubmitted;
				console.log(this.isSubmitted);
				//this.isNotHaveData = result.isNotHaveData;
			},
			(error) => {
				this.appErrorHandler.handleErrorWithNoti(error);
			}
		);
	}
}

